// -------------------------------------------------------------------------
//    Main
//--------------------------------------------------------------------------
.main {
  height: 80vh;

  section {
    margin: 0 5vw;

    & > div {
      margin: 0.5vh 0;
      
      p {
        margin: 0.7vh 0;
        font-size: calc(6px + 2vmin);
        font-weight: 500;
      }
      span {
        font-weight: 300;
      }
    }
  }

  // first section
  .current-information {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    // align-items: center;

    border-bottom: 1px solid $secondary-color;

    .left-side {
      display: flex;
      flex-flow: column nowrap;
      justify-content: space-around;

      position: relative;
      display: inline-block;

      width: 60%;

      .findlocation {
        margin: 0 0 1.4vh 0;
        
        input {
          width: 100%;
          border: none;
          background: $theme-color;
          color: $primary-color;
          font-size: calc(15px + 2vmin);
          font-weight: 500;
        }
        &::after {
          // content: '▼';
          // content: '▾';
          content: '◂';
          position: absolute;
          right: 0;
          color: $secondary-color;
          font-size: calc(15px + 2vmin);
        }
        input:focus div.chooselocation {
          visibility: visible;
        }
      }
      .chooselocation {
        position: absolute;
        margin: 1vh 0 0 0;
        width: 165%;
        border: 1px solid $secondary-color;
        background-color: $theme-color;
        z-index: 1;

        p {
          padding: 1.5vh 0px 1.5vh 2vh;
        }
      }
      .close {
        visibility: hidden;
      }
      .date {
        margin: 0.7vh 0;
        font-size: calc(6px + 2vmin);
        font-weight: 500;
      }
    }

    .right-side {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      align-items: center;

      .temperature {
        font-size: calc(18px + 2vmin);
        font-weight: 400;
      }
      .time {
        margin: 0.7vh 0;
        font-size: calc(6px + 2vmin);
        font-weight: 500;
      }
    }
  }// .first section

  // second section
  .detailed-weather {

    .weather-type {
      margin: 1vh 0 0 0;
      color: $secondary-color;
      font-size: calc(6px + 2vmin);
      text-transform: lowercase;
    }

    .head-info {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;

      .temperature {
        font-size: calc(50px + 2vmin);
        font-weight: 400;
        letter-spacing: -2px;
      }

      .realfeel {
        font-size: calc(6px + 2vmin);
        font-weight: 400;

        span {
          font-weight: 600;
        }
      }

      .description {
        margin: 2.5vh 0;
        font-size: calc(15px + 2vmin);
      }

      .weather-icon {
        align-self: center;
        width: 145px;
        height: 87px;

        img {
          width: 100%;
          height: auto;
        }
      }
      
    }

    .extra-info {
      .pressure {
        // display: none;
      }
    }
  }// .second section

  
}