@charset "UTF-8";
.header {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  height: 13vh;
  border-bottom: 2px solid #798895; }
  .header .wrapper {
    margin: 0 5vw; }
  .header h3:before {
    content: url(./../../images/logo.png);
    display: inline-block;
    vertical-align: middle;
    font-style: normal; }
  .header p {
    font-size: calc(4px + 2vmin); }

.main {
  height: 80vh; }
  .main section {
    margin: 0 5vw; }
    .main section > div {
      margin: 0.5vh 0; }
      .main section > div p {
        margin: 0.7vh 0;
        font-size: calc(6px + 2vmin);
        font-weight: 500; }
      .main section > div span {
        font-weight: 300; }
  .main .current-information {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    border-bottom: 1px solid #798895; }
    .main .current-information .left-side {
      display: flex;
      flex-flow: column nowrap;
      justify-content: space-around;
      position: relative;
      display: inline-block;
      width: 60%; }
      .main .current-information .left-side .findlocation {
        margin: 0 0 1.4vh 0; }
        .main .current-information .left-side .findlocation input {
          width: 100%;
          border: none;
          background: #282c34;
          color: #ffffff;
          font-size: calc(15px + 2vmin);
          font-weight: 500; }
        .main .current-information .left-side .findlocation::after {
          content: '◂';
          position: absolute;
          right: 0;
          color: #798895;
          font-size: calc(15px + 2vmin); }
        .main .current-information .left-side .findlocation input:focus div.chooselocation {
          visibility: visible; }
      .main .current-information .left-side .chooselocation {
        position: absolute;
        margin: 1vh 0 0 0;
        width: 165%;
        border: 1px solid #798895;
        background-color: #282c34;
        z-index: 1; }
        .main .current-information .left-side .chooselocation p {
          padding: 1.5vh 0px 1.5vh 2vh; }
      .main .current-information .left-side .close {
        visibility: hidden; }
      .main .current-information .left-side .date {
        margin: 0.7vh 0;
        font-size: calc(6px + 2vmin);
        font-weight: 500; }
    .main .current-information .right-side {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      align-items: center; }
      .main .current-information .right-side .temperature {
        font-size: calc(18px + 2vmin);
        font-weight: 400; }
      .main .current-information .right-side .time {
        margin: 0.7vh 0;
        font-size: calc(6px + 2vmin);
        font-weight: 500; }
  .main .detailed-weather .weather-type {
    margin: 1vh 0 0 0;
    color: #798895;
    font-size: calc(6px + 2vmin);
    text-transform: lowercase; }
  .main .detailed-weather .head-info {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between; }
    .main .detailed-weather .head-info .temperature {
      font-size: calc(50px + 2vmin);
      font-weight: 400;
      letter-spacing: -2px; }
    .main .detailed-weather .head-info .realfeel {
      font-size: calc(6px + 2vmin);
      font-weight: 400; }
      .main .detailed-weather .head-info .realfeel span {
        font-weight: 600; }
    .main .detailed-weather .head-info .description {
      margin: 2.5vh 0;
      font-size: calc(15px + 2vmin); }
    .main .detailed-weather .head-info .weather-icon {
      align-self: center;
      width: 145px;
      height: 87px; }
      .main .detailed-weather .head-info .weather-icon img {
        width: 100%;
        height: auto; }

.footer {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  height: 7vh;
  border-top: 2px solid #798895; }
  .footer p {
    font-size: calc(8px + 2vmin); }
  .footer .link {
    color: #61dafb;
    text-decoration: none; }

body {
  width: 100%;
  height: 100%;
  max-height: 100vh;
  background-color: #282c34;
  color: #fff;
  font-size: calc(10px + 2vmin); }
